	module.exports = {"data":{"challengeNode":{"title":"Problem 411: Uphill paths","description":["Let n be a positive integer. Suppose there are stations at the coordinates (x, y) = (2i mod n, 3i mod n) for 0 ≤ i ≤ 2n. We will consider stations with the same coordinates as the same station.","","We wish to form a path from (0, 0) to (n, n) such that the x and y coordinates never decrease.","Let S(n) be the maximum number of stations such a path can pass through.","","For example, if n = 22, there are 11 distinct stations, and a valid path can pass through at most 5 stations. Therefore, S(22) = 5.","The case is illustrated below, with an example of an optimal path:","","","","It can also be verified that S(123) = 14 and S(10000) = 48.","","Find ∑ S(k5) for 1 ≤ k ≤ 30."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-411-uphill-paths","blockName":"Project Euler","tests":[{"text":"<code>euler411()</code> should return 9936352.","testString":"assert.strictEqual(euler411(), 9936352, '<code>euler411()</code> should return 9936352.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler411() {\n  // Good luck!\n  return true;\n}\n\neuler411();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-412-gnomon-numbering","id":"5900f5081000cf542c510019"},"slug":"/coding-interview-prep/project-euler/problem-411-uphill-paths"}}

